import Component from '../react/component';
import { diff } from './diff'

const ReactDOM = {
  render,
}

function render(vnode, container, dom){
  // return container.appendChild(_render(vnode));
  console.log('vnode......................', vnode);
  return diff(dom, vnode, container);
}

export function renderComponent(comp){
  let base;
  const renderer = comp.render(); // jsxå¯¹è±¡
  base = _render(renderer);
  if(comp.base && comp.componentWillUpdate){
    comp.componentWillUpdate();
  }
  if(comp.base){
    if(comp.componentDidUpdate) comp.componentDidUpdate();
  }else{
    if(comp.componentDidMount) comp.componentDidMount();
  }

  // èŠ‚ç‚¹æ›¿æ¢
  if(comp.base && comp.base.parentNode){
    console.log('comp.base.parentNode......................', comp.base.parentNode)
    console.log('comp.base......................', comp.base)
    console.log('base.111.....................', base.nodeType)
    comp.base.parentNode.replaceChild(base, comp.base);
  }
  comp.base = base;
  console.log('comp..............', comp);
}

function setComponentProps(comp, props){
  if(!comp.base){
    if(comp.componentWillMount) comp.componentWillMount();
  }else if(comp.componentReceiveProps){
    comp.componentReceiveProps();
  }
  // è®¾ç½®ç»„ä»¶çš„å±æ€§
  comp.props = props;
  // æ¸²æŸ“ç»„ä»¶
  renderComponent(comp);

}

function createComponent(comp, props){
  let inst;
  if(comp.prototype && comp.prototype.render){
    // å¦‚æœæ˜¯ç±»å®šä¹‰çš„ç»„ä»¶ åˆ™åˆ›å»ºå®ä¾‹è¿”å›
    inst = new comp(props);
  }else{
    // å¦‚æœæ˜¯å‡½æ•°ç»„ä»¶ å°†å‡½æ•°ç»„ä»¶æ‰©å±•æˆç±»ç»„ä»¶ æ–¹ä¾¿åé¢ç»Ÿä¸€ç®¡ç†
    inst = new Component(props);
    inst.constructor = comp;
    inst.render = function(){
      return this.constructor(props);
    }
  }
  console.log('inst......................', inst)
  return inst;
}

function _render(vnode) {
  // console.log('vnode.........', vnode);
  if (vnode === undefined || vnode === null || typeof vnode === 'boolean') vnode = '';
  // å¦‚æœæ˜¯æ•°å­—è½¬æˆå­—ç¬¦ä¸²
  if(typeof vnode === 'number') vnode = String(vnode);
  // å¦‚æœvnodeæ˜¯å­—ç¬¦ä¸²
  if (typeof vnode === 'string') {
    console.log('vnode......................', vnode.nodeType)
    // åˆ›å»ºæ–‡æœ¬èŠ‚ç‚¹
    return document.createTextNode(vnode);
  }

  // å¦‚æœtagæ˜¯å‡½æ•°ï¼Œåˆ™æ¸²æŸ“ç»„ä»¶
  if(typeof vnode.tag === 'function'){
    // console.log('vnode.tag..............', vnode.tag);
    // console.log('vnode.attrs..............', vnode.attrs);
    // 1.åˆ›å»ºç»„ä»¶
    const comp = createComponent(vnode.tag, vnode.attrs);
    // console.log('comp..............', comp);
    // return;
    // 2. è®¾ç½®ç»„ä»¶çš„å±æ€§
    setComponentProps(comp, vnode.attrs);
    // 3.ç»„ä»¶æ¸²æŸ“çš„èŠ‚ç‚¹å¯¹è±¡è¿”å›
    return comp.base;
  }


  // å¦åˆ™å°±æ˜¯è™šæ‹ŸDOMå¯¹è±¡
  const {
    tag,
    attrs,
    childrens = [],
  } = vnode;
  // åˆ›å»ºèŠ‚ç‚¹å¯¹è±¡
  const dom = document.createElement(tag);
  if (attrs) {
    // æœ‰å±æ€§
    Object.keys(attrs).forEach(key => {
      const value = attrs[key];
      setAttribute(dom, key, value);
    });
  }

  // é€’å½’æ¸²æŸ“å­èŠ‚ç‚¹
  childrens.forEach(node => {
    render(node, dom)
  });

  return dom;
}

// è®¾ç½®å±æ€§
export function setAttribute(dom, key, value) {
  //å°†å±æ€§åclassNameè½¬æ¢æˆclass
  if (key === 'className') {
    key = 'class';
  }
  // å¦‚æœæ˜¯äº‹ä»¶ onClick onBlur ...
  if (/on\w+/.test(key)) {
    //è½¬å°å†™
    key = key.toLowerCase();
    dom[key] = value || '';
  } else if (key === 'style') { // é’ˆå¯¹å±æ€§ä¸ºstyleçš„å¤„ç†
    if (!value || typeof value === 'string') {
      dom.style.cssText = value || '';
    } else if (value && typeof value === 'object') {
      for (let k in value) {
        // {width: 20}
        if (typeof value[k] === 'number') {
          dom.style[k] = value[k] + 'px';
        } else {
          dom.style[k] = value[k];
        }
      }
    }
  } else {
    // console.log('dom................', dom);
    // console.log('key................', key);
    // console.log('key in dom................', key in dom);
    // å…¶ä»–å±æ€§
    if (key in dom) {
      dom[key] = value || ''
    }
    if (value) {
      // æ›´æ–°å€¼
      dom.setAttribute(key, value);
    } else {
      dom.removeAttribute(key);
    }
  }
}

export default ReactDOM;