import { setAttribute } from './index';

export function diff(dom, vnode, container){
  //å¯¹æ¯”èŠ‚ç‚¹çš„å˜åŒ–
  const ret = diffNode(dom, vnode)
  console.log('ret.......................', ret);
  if(container){
    container.appendChild(ret)
  }
  return ret;
}

function diffNode(dom, vnode){
  let out = dom;
   // console.log('vnode.........', vnode);
   if (vnode === undefined || vnode === null || typeof vnode === 'boolean') vnode = '';
   // å¦‚æœæ˜¯æ•°å­—è½¬æˆå­—ç¬¦ä¸²
   if(typeof vnode === 'number') vnode = String(vnode);
   // å¦‚æœvnodeæ˜¯å­—ç¬¦ä¸²
   if (typeof vnode === 'string') {
    // console.log('vnode......................', vnode)
    //  console.log('vnode......................', vnode.nodeType)
     if(dom && dom.nodeType === '3'){
      if(dom.textContent !== vnode){
        //æ›´æ–°å†…å®¹
        dom.textContent = vnode;
      }
     }else{
      out = document.createTextNode(vnode);
      if(dom && dom.parentNode){
        dom.parentNode.replaceNode(out, dom);
      }
     }
     // åˆ›å»ºæ–‡æœ¬èŠ‚ç‚¹
     return out;
   }
   //éæ–‡æœ¬
   if(!dom){
    out = document.createElement(vnode.tag);
   }
   // æ¯”è¾ƒå­èŠ‚ç‚¹ï¼ˆdomèŠ‚ç‚¹å’Œç»„ä»¶ï¼‰
   if(vnode.childrens && vnode.childrens.length > 0 || (out.childNodes && out.childNodes.length > 0)){
    // å¯¹æ¯”ç»„ä»¶å’Œå­èŠ‚ç‚¹
    diffChildren(out, vnode.childrens);
   }
   diffAttribute(out, vnode);
   return out;
}

function diffChildren(dom, vchildren){
  const domChildren = dom.childNodes;
  const children = [];
  const keyed = {};

  // å°†æœ‰keyçš„èŠ‚ç‚¹ï¼ˆç”¨å¯¹è±¡ä¿å­˜ï¼‰å’Œæ²¡æœ‰keyçš„èŠ‚ç‚¹ï¼ˆç”¨æ•°ç»„ä¿å­˜ï¼‰åˆ†å¼€
  if(domChildren && domChildren.length > 0){

  }
  if(vchildren && vchildren.length > 0){
    let min = 0;
    let childrenLen = children.length;
    [...vchildren].forEach((vchild, i) => {
      // è·å–è™šæ‹ŸDOMä¸­çš„æ‰€æœ‰key
      const key = vchild.key;
      let child;
      if(key){
        // å¦‚æœæœ‰keyï¼Œæ‰¾åˆ°å¯¹åº”keyå€¼çš„èŠ‚ç‚¹
        if(keyed[key]){
          child = keyed[key];
          keyed[key] = undefined;
        }
      }else if(childrenLen > min){
        // å¦‚æœæ²¡æœ‰keyï¼Œåˆ™ä¼˜å…ˆæ‰¾ç±»å‹ç›¸åŒçš„èŠ‚ç‚¹
        for(let j = min; j < childrenLen; j++){
          let c = children[j];
          if(c){
            child = c;
            children[j] = undefined;
            if(j === childrenLen - 1) childrenLen--;
            if(j === min) min++;
            break;
          }
        }
      }
      // å¯¹æ¯”
      child = diffNode(child, vchild);
      // æ›´æ–°DOM
      const f = domChildren[i];

      if(child && child !== dom && child !== f){
        // å¦‚æœæ›´æ–°å‰çš„å¯¹åº”ä½ç½®ä¸ºç©ºï¼Œè¯´æ˜æ­¤èŠ‚ç‚¹æ˜¯æ–°å¢çš„
        if(!f){
          dom.appendChild(child);
          // å¦‚æœæ›´æ–°åçš„èŠ‚ç‚¹å’Œæ›´æ–°å‰å¯¹åº”ä½ç½®çš„ä¸‹ä¸€ä¸ªèŠ‚ç‚¹ä¸€æ ·ï¼Œ
        }else if(child === f.nextSibling){
          removeNode(f);
          // å°†æ›´æ–°åçš„èŠ‚ç‚¹ç§»åŠ¨åˆ°æ­£ç¡®çš„ä½ç½®
        }else{
          // æ³¨æ„insertBeforeçš„ç”¨æ³•ï¼Œç¬¬ä¸€ä¸ªå‚æ•°æ˜¯è¦æ’å…¥çš„èŠ‚ç‚¹
          dom.insertBefore(child, f);
        }
      }
    });
  }
}

function diffAttribute(dom, vnode){
  const oldAttrs = {};
  const newAttrs = vnode.attrs;
  // dom æ˜¯åŸæœ‰çš„èŠ‚ç‚¹å¯¹è±¡   vnode æ˜¯è™šæ‹ŸDOM
  const domAttrs = dom.attributes;
  console.log('domAttrs..................', domAttrs);
  [...domAttrs].forEach(item => {
    oldAttrs[item.name] = item.value;
  });

  // æ¯”è¾ƒ
  // å¦‚æœåŸæ¥å±æ€§è·Ÿæ–°çš„å±æ€§å¯¹æ¯”ï¼Œä¸åœ¨æ–°çš„å±æ€§ä¸­ï¼Œåˆ™å°†å…¶ç§»é™¤ï¼ˆå±æ€§å€¼ä¸ºunderfineï¼‰
  for(let key in oldAttrs){
    if(!(key in newAttrs)){
      setAttribute(dom, key, undefined);
    }
  }

  // æ›´æ–° class='active' abc
  for(let key in newAttrs){
    if(oldAttrs[key] !== newAttrs[key]){
      setAttribute(dom, key, newAttrs[key]);
    }
  }
}